___CSS_LOADER_EXPORT___.push([module.id, `/* Di bawah 635px */
@media screen and (max-width: 635px) {
  /* Detail Restaurant */

  .detail-restaurant {
    margin-top: 150px;
  }

  .image-detail-wrapper img {
    width: 90%;
    height: auto;
  }

  .restaurant-detail-head {
    margin-left: 25px;
    color: black;
  }

  .restaurant-detail-head h1 {
    font-size: 1.2rem;
  }

  .rating-detail-container {
    font-size: 0.9rem;
    margin-top: 2px;
  }

  .fa-star.detail {
    color: black;
    font-size: 0.9rem;
  }

  .city-address-detail-container {
    right: auto;
    text-align: left;
    font-size: 0.8rem;
    top: auto;
    margin: 45px 27px;
  }

  .restaurant-detail-category-container {
    margin-left: 26px;
    margin-top: 10px;
    font-size: 0.7rem;
    bottom: -7px;
    position: static;
  }

  .button-detail-container {
    right: auto;
    top: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 60px auto 0 27px;
    text-align: center;
  }

  .button-detail-container .favorite {
    padding: 12px;
    cursor: pointer;
    font-size: 0.7rem;
  }

  .button-detail-container .favorite i {
    font-size: 0.8rem;
  }

  .detail-description {
    width: 90%;
    margin: 140px auto 0 auto;
    font-size: 14px;
    line-height: 1.7;
    text-align: justify;
  }
  /* End Detail Restaurant */

  /* Menus */

  .menus-detail-container {
    width: 90%;
    text-align: center;
    margin: auto;
  }

  .food-item {
    width: 100%;
  }

  .food-text,
  .drink-text {
    margin-top: 35px;
  }
  /* End Menus */

  /* Reviews */
  .review-card-container {
    gap: 5px;
  }

  .review-card {
    width: 100%;
    margin: 0 0 20px;
  }

  .review-details h3 {
    font-size: 1rem;
  }

  .review-details p {
    font-size: 0.8rem;
  }

  .review-content p {
    font-size: 0.9rem;
  }
  /* End Reviews */

  /* Review Form */
  .review-form {
    width: 90%;
  }

  /* End Review Form */
}

/* Di atas 636px dan di bawah 768px*/
@media screen and (min-width: 636px) and (max-width: 767px) {
  /* Detail Restaurant */
  .restaurant-detail-head {
    margin-left: 77px;
  }

