    font-size: 1.2rem;
  }

  .rating-detail-container {
    font-size: 0.9rem;
    margin-bottom: 4px;
  }

  .city-address-detail-container {
    right: 80px;
    top: 85%;
    font-size: 0.8rem;
  }

  .restaurant-detail-category-container {
    margin-left: 77px;
  }

  .restaurant-detail-category-container > div {
    font-size: 0.8rem;
  }

  .button-detail-container {
    right: 77px;
    top: 96%;
  }

  .button-detail-container .favorite {
    font-size: 0.6rem;
    padding: 5px;
  }

  .button-detail-container .favorite i {
    font-size: 12px;
  }
  /* Detail Restaurant */

  /* Menus */
  .menus-detail-container {
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
  }

  .foods-container,
  .drinks-container {
    width: 100%;
    display: flex;
    justify-content: start;
    flex-wrap: wrap;
  }

  .food-item,
  .drink-item {
    width: calc(33.33% - 20px);
